<template>
    <div class="address">
        <div class="modal" v-if="box">
        <div class="modal-main user-modal">

            <div class="modal-content">
                <img :src="src" alt="">    
            </div>
            <p class='paddress'>
            	<span>账户地址：</span>
            	<span>{{ address }}</span>
            </p>
        </div>
    </div>
    </div>


</template>

<script>
    //import  from ''
import ContractServies from '@/services/contract-servies';
	import ContractConfig from '@/config/contract-config';
    export default {
        //组件名
        name: 'template',
        //实例的数据对象
        data() {
            return {
                box:true,
                src:"",
                address:"",
            }
        },
        //数组或对象，用于接收来自父组件的数据
        props: {

        },
        //计算
        computed: {

        },
        //方法
        methods: {

        },
        //生命周期函数
        created() {
            this.src = 'data:image/png;base64, '+this.$route.query.img;
            this.address = ContractServies.getAddress();
        },
        beforeMount() {

        },
        mounted() {

        },
        //监视
        watch: {

        },
        //组件
        components: {

        },
        //过滤器
        filters:{

        },
        //自定义指令
        directive:{

        }
    }
</script>

<!--
	作者：liangyanxiangde@163.com
	时间：2017-03-27
	描述：统一使用less,局部样式
-->
<style lang="less" scoped>
@import "../../less/modules/base.less";
.address{

}
.modal{
  position:fixed;
  top:0;
  left:0;
  z-index:999;
  width:100%;
  height:100%;
  background:rgba(0,0,0,0.5);
  .modal-main{  //弹窗主体
    position:absolute;
    // width:50%;
    top:50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    background-color:#fff;
    border-radius:.18rem;
    .modal-content{
    	
        img{
        	margin: .25rem;
            width:5rem;
            height:5rem;
        }
        margin-bottom:.3rem;
    }
    .paddress{
    	display: flex;
    	width:5rem;
    	line-height: .5rem;
    	font-size:.24rem;
    	text-overflow: ellipsis;
    	word-break: break-all;
    	span:first-child{
    		padding: 0 0 0 0.35rem;
    		display: inline-block;
    		width: 2.5rem;
    	}
    	
    }
  }
}
</style>


